import ProjectList from "./components/ProjectList";
import ProjectStats from "./components/ProjectStats";
import {AppBar, createTheme, CssBaseline, Grid, ThemeProvider, Toolbar} from "@mui/material";
import {useEffect, useState} from "react";

import Logo from './images/logo_small.png';
import axios from "axios";

const theme = createTheme({
    palette: {
        primary: {
            main: '#872eff'
        }
    }
});

function DivvyDashboard() {

    const [activeProject, setActiveProject] = useState(0);
    const [projects, setProjects] = useState([]);

    useEffect(() => {
        axios
            .get('https://raw.githubusercontent.com/DivvySD/dashboard/main/projects.json')
            .then(resp => {
                setProjects(resp.data);
            })
    }, []);

    if(!projects.length)return <></>;

    return (
        <ThemeProvider theme={theme}>
            <CssBaseline/>
            <AppBar color="primary">
                <Toolbar>
                    <img src={Logo} height={32}/>
                </Toolbar>
            </AppBar>
            <Grid container style={{marginTop: 64}}>
                <Grid item style={{
                    width: '25%',
                    height: 'calc(100% - 64px)',
                    overflow: 'auto',
                    position: 'fixed',
                    borderRight: '1px solid #eee'
                }}>
                    <ProjectList projects={projects} update={setActiveProject} active={activeProject}/>
                </Grid>

                <Grid item xs={9} style={{
                    left: '25%',
                    height: 'calc(100% - 64px)',
                    overflow: 'auto',
                    padding: 40,
                    position: 'fixed',
                    backgroundColor: '#fafafa'
                }}>
                    <ProjectStats project={projects[activeProject]}/>
                </Grid>
            </Grid>

        </ThemeProvider>
    );
}

export default DivvyDashboard;
